import React, { useEffect, useRef, useContext } from "react";
import { useHotkeys } from "react-hotkeys-hook";
import GameContext from "./../../contexts/game";
import { rows } from "./constants";
import "./style.scss";

const Scoreboard = () => {
  const {
    rollIndex,
    players,
    currentPlayerIndex,
    dice,
    turnPlayed,
    setTurnPlayed,
    scores,
    setScores,
    turns,
    gameOver,
    lastPlay,
    setLastPlay,
    updateLog,
  } = useContext(GameContext);
  const onesRef = useRef(null);
  const twosRef = useRef(null);
  const threesRef = useRef(null);
  const foursRef = useRef(null);
  const fivesRef = useRef(null);
  const sixesRef = useRef(null);
  const threeOfKindRef = useRef(null);
  const fourOfKindRef = useRef(null);
  const fullHouseRef = useRef(null);
  const smallStraightRef = useRef(null);
  const largeStraightRef = useRef(null);
  const yhatzeeRef = useRef(null);
  const chanceRef = useRef(null);
  const undoRef = useRef(null);

  useEffect(() => {
    rows.forEach((row) => {
      if (row.assignable) {
        row.ref = eval(`${row.key}Ref`); // eslint-disable-line
      }
    });
  }, [currentPlayerIndex]);

  const hotKeyAction = (ref) => {
    ref.current.classList.add("Scoreboard-Scores-Column-Score--selected");
    ref.current.click();
    setTimeout(() => {
      ref.current.classList.remove("Scoreboard-Scores-Column-Score--selected");
    }, 500);
  };

  useHotkeys("option+1,control+1,command+1", () => hotKeyAction(onesRef));
  useHotkeys("option+2,control+2,command+2", () => hotKeyAction(twosRef));
  useHotkeys("option+3,control+3,command+3", () => hotKeyAction(threesRef));
  useHotkeys("option+4,control+4,command+4", () => hotKeyAction(foursRef));
  useHotkeys("option+5,control+5,command+5", () => hotKeyAction(fivesRef));
  useHotkeys("option+6,control+6,command+6", () => hotKeyAction(sixesRef));
  useHotkeys("option+t,control+t,command+t", () =>
    hotKeyAction(threeOfKindRef)
  );
  useHotkeys("option+f,control+f,command+f", () => hotKeyAction(fourOfKindRef));
  useHotkeys("option+h,control+h,command+h", () => hotKeyAction(fullHouseRef));
  useHotkeys("option+s,control+s,command+s", () =>
    hotKeyAction(smallStraightRef)
  );
  useHotkeys("option+l,control+l,command+l", () =>
    hotKeyAction(largeStraightRef)
  );
  useHotkeys("option+y,control+y,command+y", () => hotKeyAction(yhatzeeRef));
  useHotkeys("option+c,control+c,command+c", () => hotKeyAction(chanceRef));
  useHotkeys("ctrl+z", () => undoRef.current.click());

  useEffect(() => {
    if (!Object.keys(scores).length) {
      const nextScores = {};
      players.forEach((player) => {
        const playerScores = {};
        rows.forEach((row) => {
          playerScores[row.key] = {
            actual: null,
            possible: null,
            assignable: row.assignable,
          };
        });
        nextScores[player] = playerScores;
      });
      setScores(nextScores);
    }
  }, [players, scores, setScores]);

  useEffect(() => {
    const nextScores = { ...scores };
    if (!Object.keys(nextScores).length) return;
    const player = players[currentPlayerIndex];

    players
      .filter((p) => p !== player)
      .forEach((p) => {
        rows.forEach((row) => {
          nextScores[p][row.key].possible = null;
        });
      });
    rows
      .filter((row) => row.assignable)
      .forEach((row) => {
        nextScores[player][row.key].possible = row.calculate(dice);
      });

    setScores(nextScores);
    setTurnPlayed(false);
  }, [dice]); //eslint-disable-line react-hooks/exhaustive-deps

  const updateTotals = () => {
    const nextScores = { ...scores };
    const playerScores = nextScores[players[currentPlayerIndex]];
    playerScores.totalTop.actual =
      playerScores.ones.actual +
      playerScores.twos.actual +
      playerScores.threes.actual +
      playerScores.fours.actual +
      playerScores.fives.actual +
      playerScores.sixes.actual;
    playerScores.bonusTop.actual =
      playerScores.totalTop.actual >= 63 ? 35 : null;
    playerScores.total.actual =
      playerScores.totalTop.actual +
      playerScores.threeOfKind.actual +
      playerScores.fourOfKind.actual +
      playerScores.fullHouse.actual +
      playerScores.smallStraight.actual +
      playerScores.largeStraight.actual +
      playerScores.yhatzee.actual +
      playerScores.chance.actual +
      playerScores.bonusTop.actual +
      playerScores.bonusYhatzee.actual;
    setScores(nextScores);
    updateLog(
      `${players[currentPlayerIndex]} has a total score of ${playerScores.total.actual}`
    );
  };

  const handleMouseOver = (event, row, player) => {
    if (
      turns === 0 ||
      rollIndex === -1 ||
      turnPlayed ||
      !row.assignable ||
      player !== players[currentPlayerIndex] ||
      scores[player][row.key].actual !== null
    ) {
      return;
    }
    event.target.classList.add("Scoreboard-Scores-Column-Score--selected");
  };

  const handleMouseOut = (event) => {
    event.target.classList.remove("Scoreboard-Scores-Column-Score--selected");
  };

  const handleCellClick = (row, player) => {
    if (
      turns === 0 ||
      rollIndex === -1 ||
      turnPlayed ||
      !row.assignable ||
      player !== players[currentPlayerIndex] ||
      scores[player][row.key].actual !== null
    ) {
      return;
    }
    const nextScores = { ...scores };
    const diceValues = dice.map((die) => die.value);
    if ([...new Set(diceValues)].length === 1) {
      if (nextScores[player].yhatzee.actual > 0) {
        nextScores[player].bonusYhatzee.actual += 50;
      }
    }
    nextScores[player][row.key].actual = nextScores[player][row.key].possible;
    setLastPlay({ row, player });
    setScores(nextScores);
    updateLog(
      `${players[currentPlayerIndex]} scored ${
        nextScores[player][row.key].actual
      } points on ${row.label}`
    );
    setTurnPlayed(true);
    updateTotals();
  };

  const undoLastEntry = () => {
    const nextScores = { ...scores };
    const { row, player } = lastPlay;
    nextScores[player][row.key].actual = null;
    updateLog(`${players[currentPlayerIndex]} removed last score entry`);
    setScores(nextScores);
    updateTotals();
    setTurnPlayed(false);
  };

  return !Object.keys(scores).length ? null : (
    <div className="Scoreboard">
      <div className="Scoreboard-Scores">
        <div className="Scoreboard-Scores-RowLabels">
          <div
            className="Scoreboard-Scores-RowLabels-Label Scoreboard-Scores-Column-Header"
            style={{ opacity: "0" }}
          >
            <span role="img" aria-label="">
              üê∂
            </span>
          </div>
          {rows.map((row, i) => (
            <div key={i} className="Scoreboard-Scores-RowLabels-Label">
              {row.label}
            </div>
          ))}
        </div>
        {players.map((player) => (
          <div
            key={player}
            className={`Scoreboard-Scores-Column ${
              player === players[currentPlayerIndex]
                ? "Scoreboard-Scores-Column--current"
                : ""
            }`}
          >
            <div className={`Scoreboard-Scores-Column-Header`}>
              {player.substring(0, 2)}
            </div>
            {rows.map((row) => (
              <div
                key={row.key}
                player={player}
                className={`${row.key} Scoreboard-Scores-Column-Score ${
                  !row.assignable
                    ? "Scoreboard-Scores-Column-Score--unassignable"
                    : ""
                }`}
                onMouseOver={(event) => handleMouseOver(event, row, player)}
                onMouseOut={handleMouseOut}
                onClick={() => handleCellClick(row, player)}
                ref={players[currentPlayerIndex] === player ? row.ref : null}
              >
                {scores[player][row.key].actual !== null
                  ? scores[player][row.key].actual
                  : null}
              </div>
            ))}
          </div>
        ))}
      </div>
      <div
        className="Scoreboard-Buttons"
        style={{ position: "absolute", left: "-999999px" }}
      >
        <button
          ref={undoRef}
          onClick={undoLastEntry}
          disabled={!turnPlayed || gameOver}
        >
          undo last entry
        </button>
      </div>
    </div>
  );
};

export default Scoreboard;
