import React, { useState } from "react";
import die1 from "./../../assets/1.svg";
import die2 from "./../../assets/2.svg";
import die3 from "./../../assets/3.svg";
import die4 from "./../../assets/4.svg";
import die5 from "./../../assets/5.svg";
import die6 from "./../../assets/6.svg";
import "./style.scss";
import { useEffect } from "react";

const SVGs = [die1, die2, die3, die4, die5, die6];

const Die = (props) => {
  const { rolling, value, saved, onClick } = props;
  const [randomIndex, setRandomIndex] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      setRandomIndex(Math.floor(Math.random() * 6));
    }, 30);
    return () => {
      clearInterval(interval);
    };
  }, []);

  return rolling && !saved ? (
    <div className="Die Die-Rolling">
      <img src={SVGs[randomIndex]} alt="" />
    </div>
  ) : (
    <div
      ref={props.refRef}
      className={`Die Die-${value} ${saved ? "Die-Saved" : ""}`}
      onClick={onClick}
    >
      <img src={SVGs[value - 1]} alt={(value - 1).toString()} />
    </div>
  );
};

export default Die;
